.App {
  background-color: White;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.size {
  height: 95vh;
  min-height: 30rem;
  display: grid;
  grid-template-rows: 20%;
  gap: 1.5rem;

  background-color: rgba(5, 50, 71, 0.115);
  border-radius: 1.3rem;

  padding: 1.5rem;
  margin: 0.5rem 0px;
}
@media (max-width:750px){
  .size{
    margin: 0rem;
    border-radius: 0.5rem;

  }

}
::-webkit-scrollbar {
  width: 0rem;
}
.sizecontroler{
  width: 55vw;
}

@media (max-width:1050px){
  .sizecontroler{
    width: 65vw;
  }
  .size{
    height: 85vh;
    min-height: 30rem;


  }
}


@media (max-width:1050px){
  .sizecontroler{
    width: 85vw;
  }
}

@media (max-width:750px){
  .sizecontroler{
    width: 95vw;
  }
}
